.App {
  /* text-align: center; */
  /* display: flex; */
  /* justify-content: center; */
  /* align-items: center; */
  /* min-height: 100vh; */
  /* background-image:linear-gradient(79deg,#eeeff3,#d3d5d4,#e9eae7); */
}

.auth-form-container, .loginForm, .registerForm {
  display: flex;
  flex-direction: column;
}

.auth-form-container{
  box-shadow: 20px 20px 20px 20px rgb(234, 234, 234);
  margin-top: 10%;
  margin-left: 40%;
  width: 50vw;
}

.google{
margin-top: 2vh;
margin-bottom: 2vh;
display: flex;
justify-content: space-between;
width: 26vw;
}
.google-text{
 background-color: rgb(255, 255, 255);
 border-radius: 10px;
}

.SignInbtn{
  background-color: black;
  color: white;
  border-radius: 8px;
}

.signInButton{
  border: 2px solid black;
}

.SignIn-header{
  display: flex;
  flex-direction: column;
}
.SignIn-header-text1{
 display: flex;
 justify-content: left;
 font-size: 2rem;
 font-weight: 600;
 margin-bottom: 1vh;
}
.SignIn-header-text2{
  display: flex;
  justify-content: left;
  font-weight: 500;
  margin-bottom: 2vh;
}
.icons{
  margin-right: 1vw;
}

.loginForm{
  background-color: white;
  border-radius: 15px;
  padding: 20px;
  width: 23vw;
}

label{
  text-align: left;
}

input{
 margin: 0.25rem 0;  
 padding: 0.25rem 0;
 border: none;
 background-color:rgb(234, 234, 234);
 border-radius: 8px;
}
button{
  border: none;
  background-color: whitesmoke;
  padding: 5px;
  border-radius: 3px;
  cursor: pointer;
}

.link-btn{
  text-align: left;
  margin: 0.25rem 0;
  border: none;
  /* text-decoration: underline; */
  background: none;
  color: rgb(133, 133, 133);
  display: flex;
}
.link{
  color: rgb(52, 107, 212);
}
.link:hover{
  cursor: pointer;
}
/* Next page */
.dashboard{
margin-right: 2vw;
}

.dashboard-top{
  font-weight: 700;
  font-size: 1.25rem;
  display: flex;
  margin-bottom: 3vh;
  justify-content: space-between;
  align-items: center;
}
.dashboard-right{
display: flex;
align-items: center;
}
.dashboard-icons{
  width: 10vw;
 display: flex;
 justify-content: space-between; 
}

.dashboard-bottom{
 display: flex;
 justify-content: space-between;
}

.top-box{
  display: flex;
  justify-content: space-between;
  height: 10vh;
  width: 12vw;
  border-radius: 8px;
  padding-right: 1vw;
  padding-top:1vh;
}
.text{
  margin-top: 3vh;
  margin-left: 1.25vw;
  display: flex;
  flex-direction: column;
}
.text1{
  font-size: 1vw;
  font-weight: 400;
}
.text2{
  font-weight: 500;
  font-size: 1.5vw;
}
.user:hover{
 cursor: pointer;
}

.linechart {
  background-color: white;
  /* border: 8px solid black; */
  border-radius: 8px;
  margin-top: 5vh;
  margin-right: 2vw;
  height: 35vh;
}

/* footer */
.footer{
  display: flex;
}
.piechart{
  background-color: white;
  /* border: 8px solid black; */
  border-radius: 8px;
  margin-top: 5vh;
  margin-right: 2vw;
  height: 30vh;
  width: 40vw;
}

.schedule{
  border-radius: 8px;
  background-color: white;
  margin-top: 5vh;
  margin-left: 2vw;
  margin-right: 2vw;
  /* border: 8px solid black; */
  height: 30vh;
  width: 40vw;
  font-size: 2vh;
}

.schedule-head{
  margin-top: 2vh;
  margin-bottom: 2vh;
  margin-left: 2vw;
  margin-right: 2vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.schedule-text1{
  font-weight: 700;
}
.schedule-text2{
  color: rgb(180, 178, 178);
}
.schedules{
  border-left: 5px solid rgb(0, 0, 0);
  padding-left: 0.75vw;
  margin-top: 1vh;
}
.schedule-content{
  margin-left: 2vw;
}
.schedules-content-heading{
  color: black;
  font-weight: 600;
  margin: 0 0 0 0;
}

.schedule-content{
 color: rgb(180, 178, 178);
}

/* .board2 */
.board2{
  display: flex;
  position: absolute;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: black;
  border-radius: 20px;
  width: 20vw;
  color: white;
  height: 100vh;
  margin-left: 2vw;
}

.board-top{
 margin-top: 14vh;
 font-size: 2rem;
 font-weight: 700;
}

.board-mid{
  display: flex;
  flex-direction: column;
  height: 50vh;
/* justify-content: space-between; */
}
.board-mid-items{
  margin-top: 4vh;
  color: rgb(255, 255, 255);
  display: flex;
  align-items: center;
}
.board-mid-items:hover{
   font-weight: 600;
   cursor: pointer;
}


.board-bottom{
display: flex;
flex-direction: column;
margin-bottom: 2vh;
}

/* NextRightSide */

.NextRightSide{
  background-color: rgb(245, 245, 245);
  /* border: 3px solid black; */
  margin-left: 25vw;
  margin-top: 5vh;
  height: 100vh;
}


.mis{
  justify-content:"left";
  display:"flex"
}

@media screen and (min-width:600px){
  .auth-form-container{
  border: 1px solid white;
  padding: 2rem;
  border-radius: 15px;
}
  .board{
    top: 0px;
    left: 0px;
    display: flex;
    position: absolute;
    background-color: black;
    width: 30vw;
    justify-content: center;
    align-items: center;
    color: white;
    height: 100vh;
    font-size: 2rem;
    font-weight: 700;
  }
}
@media screen and (max-width:1002px){
  .piechart{
    width: 100%;}
  .footer{
    flex-wrap: wrap;
  }
  .schedule{
    width:100%;
  }
  .board2{
    height: 130vh;
  }
}
@media screen and (max-width:550px) {
  .board2{
    display:none
  }
  .NextRightSide{
    width: 100%;
    margin-left: 0px;
  }
  .Dashboard:hover~.board2{
         display: flex;
  }
}